h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}

html {
    font-size: 62.5%;

    overflow-x: hidden;
    scroll-behavior: smooth;

    &.active-modal {
        overflow: visible;
    }
}

body {
    margin: 0;
    
    font-size: 2rem;
    font-family: 'Montserrat', sans-serif;
    color: $white;
    line-height: 1.2;
    background: #181818;

    overflow-x: hidden;

    &.active-modal {
        overflow: hidden;
    }
}

*,
*:after,
*:before {
    box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.container {
    max-width: 1200px;
    margin: 0 auto;

    padding-left: 1.5rem;
    padding-right: 1.5rem;

    @include breakpoint($tablet-bg) {
        padding-left: 3rem;
        padding-right: 3rem;
    }

}

.container-min {
    width: 800px;
    margin: 0 auto;
    padding-left: 1.5rem;
    padding-right: 1.5rem;

    @include breakpoint($tablet-bg) {
        padding-left: 3rem;
        padding-right: 3rem;
    }
}