.post {
    display: flex;


    iframe {
        width: 100%;
    }

    margin-bottom: 3rem;
}

.post__card {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;

    background: #202020;

    padding: 3rem;
    margin: 0 1rem;
}

.post__photo {
    margin-bottom: 1.5rem;

    img {
        width: 100%;
    }
}

.post__title {
    font-size: 3.5rem;
    font-weight: 700;

    margin-bottom: 1.5rem;
}

.post__link {
    font-size: 1.7rem;
    font-weight: 500;
    color: $white;
    text-decoration: none;

    margin-bottom: 1.5rem;
}

.post__text {
    font-size: 1.5rem;
    color: $white;
    font-weight: 400;
    text-align: center;

    margin-bottom: 1.5rem;
}

.post__btn {
    font-size: 1.8rem;
    color: $black;
    background: rgb(158, 158, 158);
    border: none;
    cursor: pointer;

    padding: 1rem 3rem;

    margin-bottom: 1.5rem;
}

.post__span {
    font-size: 1.6rem;
}