.sidebar {
    width: 25%;
    background: #202020;

    padding: 3rem 0;
    display: flex;
    justify-content: center;
}

.sidebar__inner {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

}

.sidebar__img {
    text-align: center;
}

.sidebar__title {
    font-size: 2.5rem;

    margin-bottom: 1rem;
    text-align: center;
}

.sidebar__links-inner {
    display: flex;
    flex-direction: column;

    padding: 0 5rem;
}

.sidebar__link {
    font-size: 1.5rem;
    color: $white;
    font-weight: 500;

    margin-bottom: 0.7rem;

    a {
        text-decoration: none;
        color: inherit;
    }
}

.sidebar__words-inner {
    display: flex;
    flex-direction: column;

    padding: 0 5rem;
}

.sidebar__word {
    font-size: 1.5rem;
    color: $white;
    font-weight: 400;

    margin-bottom: 0.7rem;
}

.sidebar__products-inner {
    border-bottom: 1px solid $white;

    padding: 1.5rem 0;
}

.sidebar__products-photo {
    margin-bottom: 1rem;
    text-align: center;
}

.sidebar__products-link {
    font-size: 1.5rem;
    color: $white;
    font-weight: 500;

    text-align: center;

    a {
        text-decoration: none;
        color: inherit;
    }
}

.sidebar__gallery-inner {
    display: flex;
    flex-wrap: wrap;
}

.sidebar__gallery-item {
    margin: 0 10px;
    width: calc(33.33333% - 20px);

    img {
        width: 100%;
    }

    margin: 0 1rem;
}