.intro {
    margin-bottom: 4rem;
}

.intro__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.intro__img {
    margin-bottom: 2rem;

    img {
        width: 100%;
    }
}

.intro__title {
    font-size: 5rem;
    color: $white;
    font-weight: 700;

    margin-bottom: 2rem;
}

.intro__text {
    font-size: 1.8rem;
    color: $white;
    font-weight: 400;

    text-align: center;
}